video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
}

.hero-container {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative; /* Ensure this is set */
}

.hero-container > h1{
    color: #fff;
    font-size: 100px;
    margin-top: -100px;
}

.hero-container > p{
    margin-top: 8px;
    color: #fff;
    font-size: 32px;
}
.hero-btns{
    margin-top: 32px;
}

.hero-btns .btn {
    margin: 6px;
}
.fa-play-circle{
    margin-left: 4px;
}

@media screen and (max-width: 960px){
    .hero-container > h1{
        font-size: 70px;
        margin-top: -150px;
    }
}
@media screen and (max-width: 768px){
.hero-container > h1{
    font-size: 50px;
    margin-top: -100px;
}
 .hero-container > p{
    font-size: 30px;
 }
 .btn-mobile{
    display: block;
    text-decoration: none;
 }
 .btn{
    width: 100%;
 }
}

    